
$black:#101B24;
$ivory:#F8F4E7;
$blown:#525053;
$green:#274000;
$blue:#29516e;
$navy:#0d2030;
$skyblue:#bdd6ce;
$gray:#C4C4C4;
$skybule_clear:rgba(189,214,206,0.7);
$blown_clear:rgba(82,80,83,0.5);
$green_clear:rgba(39,64,0,0.5);
$navy_clear:rgba(13,32,48,0.5);

$xxxs:1.2rem;
$xxs:1.4rem;
$xs:1.6rem;
$s:1.8rem;
$m:2.0rem;
$l:2.4rem;
$xl:3.6rem;
$xxl:4.8rem;

$spmargin:100px 16px;

$colorList:(
    green:$green,
    blown:$blown,
    blue:$blue,
    navy:$navy,
    skyblue:$skyblue
);


@mixin media_PC($width-media:1390px){
    @media only screen and
      (min-width:$width-media){
      @content;
    }
  }
  
@mixin media_tablet($width-media:768px){
  @media only screen and
    (min-width:$width-media){
    @content; 
    }
}

@mixin hamburger{
    display: block;
    width: 24px;
    height: 2px;
    background-color:$black;
    @include media_PC{
      width: 36px;
    }
}
@mixin header_tablet{
  height: 80px;
}
@mixin layout_tablet{
  max-width: 750px;
  margin: 100px auto;
}
@mixin layout_PC {
  max-width: 920px;
  margin: 100px auto;
}
@mixin  layout_PCwide{
  max-width: 1300px;
  margin: 150px auto;
}
@mixin center{
  position: absolute;
  top: 50%; /*親要素を起点に上から50%*/
  left: 50%;  /*親要素を起点に左から50%*/
  transform: translateY(-50%) translateX(-50%); /*要素の大きさの半分ずつを戻す*/
}
@mixin scroll{
  position: absolute;
  width: 2px;
  height: 150px;
  background-color: #fff;
  animation: pathmove 1.4s ease-in-out infinite;
  animation-delay : 3s;
  opacity:0;
  transition-duration: 1s;
  &::after{
      content: "";
      position: absolute;
      background-color: white;
    }
  @keyframes pathmove{
      0%{
        bottom:30px;
        height: 150px;
        opacity: 0;
      }
      30%{
        height:150px;
        opacity: 1;
      }
      80%{
          height:100px;
          opacity: 1;
      }
      100%{
        height:10px;
        bottom:0;
        opacity: 0;
      }
    }
}
%buttonBefore{
  position: absolute;
  content: "";
  bottom:0;
  left: 0;
}
%buttonAfter{
  position: absolute;
  transform: rotate(-45deg); 
  content: "";
}
%addItem{
  position: absolute;
  content: "";
  width: 0;
  height: 0;
}
%img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
html {
    font-size: 62.5%;
    font-family: 'Shippori Mincho', serif;
    color:$black;
    font-weight: normal;
    scroll-behavior: smooth;
    background-color:$ivory;
}
h2,h3,h4{
  font-weight: normal;
}
a{
  color : inherit;
  text-decoration : none;
}
ul{
  list-style: none;
}