@use '../fontdation/base';

@each $colorname, $color in base.$colorList{
    .u-back--#{$colorname}{
        background-color: $color;
    }
}

@each $colorname, $color in base.$colorList{
    .u-font--#{$colorname}{
        color: $color;
    &::before{
        border-color: $color transparent transparent transparent;
    }}
}